import { connect } from 'react-redux'
import { StatForm } from './ui';
import { setComparisonStats } from '../state/actions';
import { getComparisonStats } from '../state/selectors';

const mapStateToProps = state => ({
    stats: getComparisonStats(state),
});

const mapDispatchToProps = dispatch => ({
    setComparisonStats: stats => dispatch(setComparisonStats(stats)),
});

export const ComparisonStatForm = connect(
    mapStateToProps,
    mapDispatchToProps
)(StatForm);